<template>
  <div class="contain">
    <div class="project-title">
      
    </div>
    <div class="project-introduce row">
      <div class="col-xs-12 col-sm-10 col-md-10">
        <img :src="websiteInfo.imageUrlPre + projectInfo.introduce" alt="">
      </div>
      <div class="col-xs-12 col-sm-2 col-md-2">
        <a :href="projectInfo.applyProUrl" style="width: 120px;" class="btn btn-primary active" role="button" target="_blank"></a>
      </div>
    </div>
    <div class="project-list" :data="projectInfo.listData">
      <div class="project-item row" v-for="item in projectInfo.listData" :key="item.id">
        {{ item.project_name }}
      </div>
    </div>
  </div>
</template>

<script>
import {mapGetters} from 'vuex'

export default {
  data () {
    return {

    }
  },
  computed: {
    ...mapGetters({
      projectInfo: 'projectInfo',
      websiteInfo: 'websiteInfo'
    })
  }
}
</script>

<style scoped>
.project-introduce.row {
  margin: 0;
  background: #ffffff;
  box-sizing: border-box;
  padding: 20px 10vw;
}
.project-title {
  width: 100%;
  font-size: 18px;
  font-weight: 700;
  box-sizing: border-box;
  padding: 20px 10vw;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background: #ffffff;
}
.project-introduce>div {
  display: flex;
  justify-content: space-around;
  align-items: center;
  max-height: 180px;
  box-sizing: border-box;
  padding: 20px;
}
.project-introduce>div>img {
  width: 80%;
}
.project-list {
  box-sizing: border-box;
  padding: 20px 10vw;
}
.project-item.row {
  margin: 0;
}
</style>



